import React, { Component } from "react";
import "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "./user.css";
import { Redirect } from "react-router-dom";
import AuthContext from "./authcontext";
class User extends Component {
  state = {
    redirect: false,
  };
  toggle=()=> {
    this.setState({ redirect: true });
  }
  render() {
    let id = this.props.match.params.id * 1;
    if (this.state.redirect) {
  
      return <Redirect to="/" />;
    } else {
      return (
        <AuthContext.Consumer>
          {(context) => (
            <div className="cardProfile">
              <img
                className="Img"
                src={context.items[id - 1001].Image}
                alt="img"
              />
              <h1>{context.items[id - 1001].name}</h1>
              <p className="title">Profile Details</p>
              <button
                className="buttonProfile btn btn-primary"
                onClick={this.toggle}
              >
                Shortlist
              </button>
              <button
                className="buttonProfile btn btn-secondary"
                onClick={this.toggle}
              >
                Reject
              </button>
            </div>
          )}
        </AuthContext.Consumer>
      );
    }
  }
}

export default User;
