import React, { Component } from "react";
import "../node_modules/bootstrap/dist/css/bootstrap.min.css";
import { BrowserRouter, Route } from "react-router-dom";
import axios from "axios";
import Home from "./Components/home";
import User from "./Components/user";
import Authcontext from "./Components/authcontext";
import Shortlisted from "./Components/shortlisted";
import "./App.css";

class App extends Component {
  state = {
    loading: true,
    items: [],
    error: null,
    short: [],
    rej: [],
  };
  componentDidMount() {
    axios
      .get(
        "https://cors-anywhere.herokuapp.com/https://s3-ap-southeast-1.amazonaws.com/he-public-data/users49b8675.json",
        {}
      )
      .then((res) => {
        this.setState({ loading: false, items: res.data });
      })
      .catch((error) => {
        this.setState({ loading: false, error });
      });
  }
  render() {
    const { error, loading } = this.state;
    if (error) {
      return <div>Error: {error.message}</div>;
    } else if (loading) {
      return <div>Loading...</div>;
    } else {
      return (
        <BrowserRouter>
          <div className="App">
            <Authcontext.Provider value={this.state}>
              <Route
                exact
                path="/"
                component={() => <Home state={this.state.items} />}
              />
              <Route path="/:id" component={User} />
              <Route exact path="/shortlisted" component={Shortlisted} />
              <Route exact path="/rejected" component={User} />
              {/* <Route component={Error} /> */}
            </Authcontext.Provider>
          </div>
        </BrowserRouter>
      );
    }
  }
}

export default App;
